const { GAPI_KEY } = process.env

export async function fetchBooks(searchConfig: any) {
	const {
		searchWord = searchConfig.searchConfig.search,
		startIndex,
		sorting,
		category,
	} = searchConfig.searchConfig;

	const response = await fetch(
		`https://www.googleapis.com/books/v1/volumes?q=${searchWord}&subject:${category}&orderBy=${sorting}&startIndex=${startIndex}&key=${GAPI_KEY}` // AIzaSyAiAERcpGEYcW42YWIGNBdar-rU8L6v5nI
	);
	return await response.json();
}

export async function getFullBook(data: any) {
	const { selfLink } = data;

	const response = await fetch(selfLink);
	return await response.json();
}