import { FETCH_BOOKS, FETCH_BOOKS_SUCCESS, FETCH_BOOKS_ERROR, ADD_MOREBOOKS } from './types';

interface CardState {
	fetchedCards: any;
	isLoadMore: boolean;
	isLoading: boolean,
	error: any
}

interface CardsAction {
	type: string;
	payload?: any;
}

const initialState: CardState = {
	fetchedCards: [],
	isLoadMore: true,
	isLoading: false,
	error: []
};

export const cardsReducer = (state = initialState, action: CardsAction): CardState => {
	switch (action.type) {
		case FETCH_BOOKS:
			console.log('FETCH_BOOKS',action.payload)
			return {
				...state,
				fetchedCards: [],
				isLoadMore: true,
				isLoading: true,
				error: []
			};
		case FETCH_BOOKS_SUCCESS:
			console.log('FETCH_BOOKS_SUCCESS',action.payload)
			if (action.payload.items.length < 10) {
				state.isLoadMore = false;
			}

			const array = [
				...new Map(
					action.payload.items.map((item: any) => [item.id, item])
				).values(),
			];

			return {
				...state,
				fetchedCards: {
					items: array,
					kind: action.payload.kind,
					totalItems: action.payload.totalItems,
				},
				isLoading: false,
				error: action.payload.error
			};

			
		/* case FETCH_BOOKS_ERROR:
			console.log('FETCH_BOOKS_ERROR', action.payload)

			return {
				...state,
				fetchedCards: [],
				isLoadMore: true,
				isLoading: false,
				error: action.payload
			}; */

		case ADD_MOREBOOKS:
			if (action.payload.items.length < 10) {
				state.isLoadMore = false;
			}

			const concatArray = state.fetchedCards.items.concat(action.payload.items);
			const newArray = [
				...new Map(concatArray.map((item: any) => [item.id, item])).values(),
			];

			return {
				...state,
				fetchedCards: {
					items: newArray,
					kind: action.payload.kind,
					totalItems: action.payload.totalItems,
				},
			};

		default:
			return state;
	}
};
