import React from 'react';
import './App.css';

/* REDUX */
import {
	requestSearch,
	addMoreBooks,
	addSearchConfig,
	updateSearchConfig,
} from '../../redux/actions';
import { useDispatch, useSelector } from 'react-redux';

/* КОМПОНЕНТЫ */
import Header from '../Header/Header';
import Books from '../Books/Books';
import BookPage from '../BookPage/BookPage';

/* ROUTES */
import { Route, Switch, useHistory, Redirect } from 'react-router-dom';

function App() {
	const dispatch = useDispatch();
	const history = useHistory();
	const [isBooksArray, setIsBooksArray] = React.useState([]);

	interface RootState {
		search: any,
		cards: any
		fullbook: any
	}

	const { searchWord, sorting, category } = useSelector(
		(state: RootState) => state.search);

	const index = useSelector((state: RootState) => state.search.startIndex);

	const books = useSelector((state: RootState) => state.cards.fetchedCards.items);
	
	/* Preloader перед загрузкой книг */
	const isLoading = useSelector((state: RootState) => state.cards.isLoading);

	const { fullbook, showFullBook } = useSelector((state: RootState) => state.fullbook);

	/* Кнопка MORE добавляет больше книг */
	function handleClickMoreBooks() {
		console.log('searchWord', searchWord);

		if (books.length) {
			const startIndex = index + 10;
			const searchConfig = {
				search: searchWord,
				startIndex: startIndex,
				sorting: sorting,
				category: category,
			};
			dispatch(addMoreBooks(searchConfig));
			dispatch(updateSearchConfig(searchConfig));
		}
	}

	/* Поиск книг */
	function handleSearchSubmit(searchString: any) {
		const searchWord = searchString.search;
		const sorting = searchString.sorting;
		const category = searchString.category;
		const startIndex = 0;

		const searchConfig = { searchWord, startIndex, sorting, category };

		dispatch(requestSearch(searchConfig));
		dispatch(addSearchConfig(searchConfig));

		setIsBooksArray([]);
		history.push('/');
	}

	/* Вывод только конкретной категории или всех сразу */
	React.useEffect(() => {
		if (books) {
			const itemExist = books.filter((item: any) => {
				if (item.volumeInfo.categories && category) {
					return typeof item === 'object';
				}
				return null;
			});

			const array = itemExist.filter((item: any) => {
				function upFirst(str: any) {
					if (!str) return str;

					return str[0].toUpperCase() + str.slice(1);
				}

				return item.volumeInfo.categories.includes(upFirst(category));
			});

			if (category !== '') {
				setIsBooksArray(array);
			} else setIsBooksArray(books);
		}
	}, [books, category]);

	return (
		<div className='page'>
			<div className='page__container'>
				<Header onSearch={handleSearchSubmit} />
				<Switch>
					<Route exact path='/'>
						<Books books={isBooksArray} onClickMore={handleClickMoreBooks} isLoading={isLoading} />
					</Route>
					<Route exact path='/book'>
						<BookPage fullbook={fullbook} />
					</Route>
				</Switch>
				<Route path='/'>
					{!showFullBook ? <Redirect to='/' /> : <Redirect to='/book' />}
				</Route>
			</div>
		</div>
	);
}

export default App;
