import React from 'react';
import './Books.css';
import BookCard from '../BookCard/BookCard';
import { useSelector } from 'react-redux';
import Preloader from '../Preloader/Preloader';
const Books = ({ books, onClickMore, isLoading } : any) => {
	interface RootState {
		cards: any	
	  }

	const isLoadMore = useSelector((state: RootState) => state.cards.isLoadMore);
	
	function handleMoreBooks() {
		onClickMore();
	}

	return (
		<section className='books'>
		{isLoading 
			? <Preloader />  :<ul className='books-items'>
				{books && books.map((item: any) => <BookCard item={item} key={item.id} />)}
			</ul> }

			{isLoadMore && books.length && (
				<button
					type='button'
					className='books__btn'
					onClick={handleMoreBooks}
					disabled={!isLoadMore}
				>
					more
				</button>
			)}
		</section>
	);
};

export default Books;
